const REGEX_EMAIL = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
const REGEX_PHONE = /^[0]([0-9]{9})*$/
const REGEX_NUMBER = /^[0-9]*$/
export default {
  data(){
    return {
      form: {
        email: this.$store.getters.getRegister.email,
        phone: this.$store.getters.getRegister.phone,
        birthday: this.$store.getters.getRegister.birthday,
      },
      modal: false,
      emailValidated: false,
      phoneValidated: false,
      emailRules: [ value => this.emailValidator(value)],
      phoneRules: [ value => this.phoneValidator(value)]
    }
  },
  methods: {
    phoneValidator(value){    
      this.phoneValidated = false  
      if(value == ''){
        return 'required'
      }
      if(REGEX_PHONE.test(value) && value.length == 10){ 
        this.phoneValidated = true
        return true
      }
      return "please input phone number"
    },
    emailValidator(value){
      this.emailValidated = false
      if(value == ''){
        return 'required'
      }
      if(REGEX_EMAIL.test(value)){
        this.emailValidated = true
        return true
      }
      return "email is Invalid"
    },
    onlyNumber(event, max){  
      if(event.target.value.length == 0){
        if(event.key != 0){
          return event.preventDefault()
        }
      }else{
        if(!REGEX_NUMBER.test(event.key) || event.target.value.length == max){
          return event.preventDefault()
        }
      }      
    }, 
    validate(){
      let validated = true
      const errors = []
      const validatorField = [
        'email',
        'phone'
      ]
      validatorField.forEach((field) => {
        if(this.form[field] == ''){
          validated = false
          errors.push(`${field} can not be null`)
        }
      })    
      if(!this.emailValidated){
        validated = false
        errors.push(`email is Invalid`)
      }
      if(!this.phoneValidated){
        validated = false
        errors.push(`please input phone number`)
      }
      if(!validated){
        this.$store.dispatch('setDialog', {
          isShow: true,
          title: 'Form is error',
          message: errors.map((error) => error+'<br/>').join('')
        })        
      }      
      return validated
    },  
    back() {      
      this.$router.push('/register')
    },
    register() {
      if(this.validate()){
        this.$store.dispatch('setRegister', this.form)
        this.$axios.patch(`https://ionic-5a65a.firebaseio.com/members/${this.$store.getters.getLine.userId}/profile.json`, this.$store.getters.getRegister).then((res) => {
          this.$router.push('/register/done')
        }).catch(e => console.log(e))         
      }      
    }
  }
}
